.reversed-corner {
  position: absolute;
  z-index: 2;
}
.reversed-corner div {
  position: relative;
  width: 180px;
  height: 180px;
  overflow: hidden;
}
.reversed-corner div::before {
  position: absolute;
  display: block;
  width: 200%;
  height: 200%;
  border-radius: 50%;
  content: "";
}
.reversed-corner_up {
  top: 0;
  right: 0;
  transform: rotate(90deg);
}
.reversed-corner_down {
  bottom: 0;
  left: 0;
  transform: rotate(-90deg);
}
.reversed-corner_down-right {
  right: 0;
  bottom: 0;
  transform: rotate(-180deg);
}
.reversed-corner_white div::before {
  box-shadow: -100px -100px 0 0 #fff;
}
.reversed-corner_blue div::before {
  box-shadow: -100px -100px 0 0 #0075dd;
}
.reversed-corner_light-blue div::before {
